<template>
	<div id="addgoods">
		<!--顶部菜单-->
		<nav-bar class="addgoods-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				新增货品
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--滚动-->
		<scroll class="addgoods-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<add-goods-content :base-info="baseInfo"/>
		</scroll>
		<!--执行按钮-->
		<action-btn @zhixingClick="zhixingClick" :btn-name="btnName" />
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import ActionBtn from 'components/common/bottombtn/ActionBtn'

import AddGoodsContent from './childComps/AddGoodsContent'

import {seedAddGoodsData} from 'network/product'



export default {
  name: 'AddGoods',
	components: {
		NavBar,
		Scroll,
		AddGoodsContent,
		ActionBtn
	},
	data(){
		return{
			btnName:'保存',
			baseInfo:{
				"productName":'',				//商品名称
				"monovalent":'',				//单价
				"specs":'',						//规格型号
				"weight":'',					//重量
				"encoded":'',					//商品编号
				"productTXM":'',				//条形码
				"productType":'',				//货品类型
				"cid":0,					    //这里需要的是id
				"warehouseid":'',				//所属仓库
				'costs':'',						//成本价
				'price':'',						//出库参考价
				'stock':'',						//库存
				'color':'',						//颜色
			}
		}
	},
	methods:{
		//返回按钮
		backClick(){
			//this.$router.replace("/customersdetail").catch(res=>{res})
			this.$router.go(-1)
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//保存
		zhixingClick(){
			//弹窗成功后-如果成功跳转回首页
			this.seedAddGoodsData(this.baseInfo)
			//console.log(this.baseInfo)
			//this.$router.replace('/home')
		},
		//网络请求
		seedAddGoodsData(obj){
			let _self=this
			seedAddGoodsData(obj).then(res=>{
				//这里是后台传出来的
				//console.log(res)
				this.$Toast.loading({
					message: '生成中...',
					forbidClick: true,
					onClose(){
						_self.$Toast(res)
						//成功后清空页面跳转
						_self.$router.go(-1)
					}
				});
			})	
		},
	},
	activated(){
		this.baseInfo.productType=this.$store.state.selectClassify.title
		this.baseInfo.cid=this.$store.state.selectClassify.id
	}
}
</script >

