<script>
import ItemListBox from 'components/contents/input/ItemListBox'
import ItemInput from 'components/contents/input/ItemInput'

import showAndHiddenItem from './showAndHiddenItem'
import InputSelect from './InputSelect'
import {uploadImg} from 'network/product'

export default {
  name: 'AddGoodsContent',
  components: {
	ItemListBox,
	ItemInput,
	showAndHiddenItem,
	InputSelect
  },
  props:{
	baseInfo:{
		type:Object,
		default(){
			return {}
		}
	}
  },
  data(){
	return{
		imgData:{
			accept:['image/gif','image/jpeg','image/png','image/jpg'],
		},
		showImg:[]
	}
  },
  methods:{
	addIconClict(){
		console.log('添加')
	},
	//上存
	fileInput(e){
		//获取
		let img1=e.target.files[0]
		if(!img1)return false
		
		let type=img1.type
		let size=img1.size
		
		//判断格式
		if(this.imgData.accept.indexOf(type)===-1){
			console.log('请选择我们支持的图片格式！')
			return false
		}
		//判断长度
		if(size>3145728){
			console.log('请选择3M以内的图片')
			return false
		}
		
		//包装数据
		let formdata=new FormData()
		formdata.append("file",img1);
		
		
		console.log(formdata.get('file'))
		//这里开始发送
		uploadImg(formdata).then(res=>{
			//console.log(res)
			this.$toast.show(res,5000)
		})
	}
  },
  computed:{
	keyInfoError(){
		if(!isNaN(this.baseInfo.productName)){
			return this.baseInfo.productName
		}else{
			return "不是数字"
		}
	}
  }
	
}
</script >

